import location from '../../components/location/location.vue';
import amapFile from '../../common/amap-wx.js';
import { mapGetters } from 'vuex';
import { key } from '../../common/config.js';

export default {
	data() {
		return {
			myAmapFun: null,
			searchValue: '',
			areaList: []
		};
	},
	watch: {
		searchValue() {
			this.searchPlace();
		}
	},
	onLoad() {},
	computed: {
		...mapGetters(['city', 'locationAddr'])
	},
	components: {
		location
	},
	methods: {
		onKeyInput: function(event) {
			this.searchValue = event.target.value;
		},
		searchPlace() {
			if (this.searchValue != '') {

				this.appWeiXing();





			}
		},
		appWeiXing() {
			var that = this;
			that.myAmapFun = new amapFile.AMapWX({ key });
			uni.showLoading({
				title: '加载中...'
			});
			that.myAmapFun.getInputtips({
				keywords: that.searchValue,
				city: that.city,
				citylimit: true,
				success: function(data) {
					uni.hideLoading();
					if (data && data.tips) {
						that.areaList = data.tips;
						console.log(that.areaList);
					}
				},
				fail: function(info) {
					uni.hideLoading();
					//失败回调
					console.log(info);
				}
			});
		},
		apph5() {
			const self = this;
			// console.log(this.search_val);
			AMap.plugin('AMap.Autocomplete', function() {
				// 实例化Autocomplete
				var autoOptions = {
					//city 限定城市，默认全国
					city: self.city
				};
				var autoComplete = new AMap.Autocomplete(autoOptions);
				autoComplete.search(self.searchValue, function(status, result) {
					// 搜索成功时，result即是对应的匹配数据
					console.log(result);
					self.areaList = result.tips;
				});
			});
		}
	}
};
